<template>
  <div class="col-10">
    <div class="sold_back">
      <h4 id="sold_sale_logo"> My sales list </h4>
      <div id="sold_fb">
        <table class="table table-borderless" id="wrote_table">
          <thead>
            <tr id="wrote_tr">
              <th scope="col"></th>
              <th scope="col" colspan="2"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody v-for="(data, index) in goods" :key="index" id="sold_tbody">
            <tr>
              <td>{{ data.category }}</td>
              <td colspan="2" id="cart_td">
              <router-link :to="'/dapanda/detail/' + data.goodsId"> {{ data.title }}
              </router-link></td>
              <td>{{ data.insertTime }}</td>
              <td>{{ data.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import UserDataService from "../../services/UserDataService";
import GoodsDataService from '@/services/GoodsDataService';

export default {
  props: ["soldProps"],
  data() {
    return {
      goods: [],
      currentUser: null,
      nick: this.soldProps,
    };
  },
  methods: {
    getsold() {
      UserDataService.findGoodsByUsername(this.nick) // spring 
        //   then  
        .then((response) => {
          this.goods = response.data;
          console.log(response.data);
        })
        //   catch  
        .catch((e) => {
          console.log(e);
        });
    },

    // 
        retrieveGoods() {
      GoodsDataService.getAll(
        this.category,
        this.title, 
        this.page - 1, 
        this.pageSize)
        .then((response) => {
          const { goods, totalItems } = response.data; // springboot    
          this.goods = goods; //   
          this.count = totalItems; //   ( )
          //    
          console.log(response.data);
        })
        //  .catch()   
        .catch((e) => {
          console.log(e);
        });
    },

  },

  mounted() {
    this.getsold();
    this.retrieveGoods();
  },
};
</script>

<style>
@import url(@/assets/css/style.css);
</style>
