<template>
  <!-- AddDiary.vue -->
  <div class="submit-form">
    <!--     -->
    <div v-if="!submitted" div class="col-md-6" id="diary_add">
      <!--  -->
      <div class="form-group">
        <label for="dtitle"></label>
        <input
          type="text"
          class="form-control"
          id="dtitle"
          required
          v-model="diary.dtitle"
          name="dtitle"
        />
      </div>
      <br />
      <!--  -->
      <div class="form-group">
        <label for="dcontent"></label>
        <textarea
          class="form-control"
          id="dcontent"
          required
          v-model="diary.dcontent"
          name="dcontent"
          style="
            display: inline;
            height: 300px;
            white-space: normal;
            text-align: start;
          "
        />
      </div>
      
      <button
        type="button"
        @click="saveDiary"
        class="btn btn-secondary"
        id="diary_modal_edit"
      >
           
      </button>
    </div>
    <!--     -->

    <div v-else>
      <h4> !</h4>
      <br />
      <span class="badge bg-dark" @click="newDiary"></span>
    </div>
  </div>
</template>

<script>
// axios  import
import DiaryDataService from "../../services/DiaryDataService";

export default {
  data() {
    return {
      diary: {
        dno: null,
        dtitle: "",
        dcontent: "",
        userId: ""
      },
      // submit   true  , You submitted successfully!  
      submitted: false,
    };
  },
  methods: {
    saveDiary() {
      //    -> springboot 
      // (no)   
      let data = {
        dtitle: this.diary.dtitle,
        dcontent: this.diary.dcontent,
        userId: this.$store.state.auth.user.id
      };

      // insert   (axios  )
      DiaryDataService.create(data)
        //  then()  
        .then((response) => {
          this.diary.dno = response.data.dno;
          //   (response.data)
          console.log(response.data);
          //  submitted
          this.submitted = true;
          location.reload();
        })
        //  .catch()  
        .catch((e) => {
          console.log(e);
        });
    },
    newDiary() {
      //    ,  
      this.submitted = false;
      this.diary = {};
    },
  },
};
</script>

<style>
@import url(@/assets/css/style.css);
</style>
