<template>
  <!-- DiaryList.vue -->

  <div id="diary_back">
    <!--     -->
    <div>
      <div>
        <h4 id="diary_logo"> DIARY </h4>
      </div>

      <div class="diaryList">
        <ul class="list-group">
          <li
            id="diary_title"
            class="list-group-item"
            :class="{ active: index == currentIndex }"
            v-for="(data, index) in diary"
            :key="index"
            @click="setActiveDiary(data, index)"
          >
            {{ data.dtitle }}
          </li>
        </ul>
      </div>
    </div>
    <!--     -->
    <div></div>
    <!--     -->
    <div class="editDiary">
      <div v-if="currentDiary" id="diary_content">
        <div>
          <label>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              class="bi bi-chat-square-quote"
              viewBox="0 0 16 16"
            >
              <path
                d="M14 1a1 1 0 0 1 1 1v8a1 1 0 0 1-1 1h-2.5a2 2 0 0 0-1.6.8L8 14.333 6.1 11.8a2 2 0 0 0-1.6-.8H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v8a2 2 0 0 0 2 2h2.5a1 1 0 0 1 .8.4l1.9 2.533a1 1 0 0 0 1.6 0l1.9-2.533a1 1 0 0 1 .8-.4H14a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"
              />
              <path
                d="M7.066 4.76A1.665 1.665 0 0 0 4 5.668a1.667 1.667 0 0 0 2.561 1.406c-.131.389-.375.804-.777 1.22a.417.417 0 1 0 .6.58c1.486-1.54 1.293-3.214.682-4.112zm4 0A1.665 1.665 0 0 0 8 5.668a1.667 1.667 0 0 0 2.561 1.406c-.131.389-.375.804-.777 1.22a.417.417 0 1 0 .6.58c1.486-1.54 1.293-3.214.682-4.112z"
              />
            </svg>
            <h4 id="diary_pen">{{ currentDiary.dtitle }}</h4></label
          >
        </div>
        <div id="diary_textarea">
          <label><strong></strong></label>
          {{ currentDiary.dcontent }}
        </div>
        <div>
          <label><strong></strong></label>
          {{ currentDiary.insertTime }}
        </div>

      

        <!--  -->
        <!-- Button trigger modal -->
        <button
          id="diary_modal_edit"
          type="button"
          class="btn btn-outline-secondary"
          data-bs-toggle="modal"
          data-bs-target="#exampleModala"
          style="
            width: 100px;
            font-size: 12px;
            height: 50px;
            text-align: center;
            background-color: lavender;
          "
        >
          
        </button>

        <div
          class="modal fade"
          id="exampleModala"
          tabindex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog modal-lg">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">EDIT</h5>
                <button
                  type="button"
                  class="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div class="modal-body">
                <!-- DiaryDetail.vue -->
                <div>
                  <!--     -->
                  <div v-if="currentDiary" class="edit-form" id="diary_add">
                    <div>
                      <form>
                        <div class="form-group">
                          <label for="dtitle"></label>
                          <input
                            type="text"
                            class="form-control"
                            id="dtitle"
                            v-model="currentDiary.dtitle"
                          />
                        </div>
                        <div class="mt-3 form-group">
                          <label for="dcontent"></label>
                          <textarea
                            type="text"
                            class="form-control"
                            id="dcontent"
                            v-model="currentDiary.dcontent"
                            style="
                              display: inline;
                              height: 300px;
                              white-space: normal;
                              text-align: start;
                            "
                          />
                        </div>
                      </form>

                      <div>
                        <button
                          class="btn"
                          @click="updateDiary"
                          id="diary_Detail_badge"
                          style="
                            width: 100px;
                            font-size: 12px;
                            height: 50px;
                            text-align: center;
                            background-color: gray;
                          "
                        >
                          
                        </button>
                        <button
                          class="btn"
                          type="submit"
                          @click="deleteDiary"
                          id="diary_Detail_badge"
                          style="
                            width: 100px;
                            font-size: 12px;
                            height: 50px;
                            text-align: center;
                            background-color: gray;
                          "
                        >
                          
                        </button>
                      </div>
                    </div>
                    <!--     -->
                  </div>
                  <div v-else>
                    <br />
                    <p> !</p>
                  </div>
                </div>

                <!-- =========================== -->
              </div>
              <div class="modal-footer">
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                  data-bs-dismiss="modal"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <br />
        <p> !</p>
      </div>

   

      <!-- Button trigger modal -->
      <button
        type="button"
        class="btn"
        data-bs-toggle="modal"
        data-bs-target="#exampleModal"
        style="
          width: 100px;
          font-size: 12px;
          height: 50px;
          text-align: center;
          background-color: lavenderblush;
        "
      >
        
      </button>

      <!--  -->
      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-lg">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">WRITE</h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="modal-body">
              <AddDiary></AddDiary>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-outline-secondary"
                data-bs-dismiss="modal"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--     -->
  </div>
</template>

<script>
// axios  import
import DiaryDataService from "../../services/DiaryDataService";
import AddDiary from "./AddDiary.vue";

export default {
  //    : data(), 
  props: ["idProps"],
  data() {
    return {
      diary: [],
      currentDiary: null,
      currentIndex: -1,
      dtitle: "",
      id: this.idProps,
    };
  },
  components: {
    AddDiary,
  },
  //    : methods:
  methods: {
    // axios ,      
    retrieveDiary() {
      DiaryDataService.getAll(this.id)
        //  .then()  
        .then((response) => {
          this.diary = response.data; // springboot    
          //    
          console.log(response.data);
        })
        //  .catch()   
        .catch((e) => {
          console.log(e);
        });
    },
    //    ,   
    setActiveDiary(data, index) {
      this.currentDiary = data;
      this.currentIndex = index;
    },
    // ===================

    //  (dno)   
    getDiary() {
      // axios  
      DiaryDataService.get(this.id)
        //  .then()  
        .then((response) => {
          // springboot  ( )
          this.currentDiary = response.data;
          //   
          console.log(response.data);
        })
        //  .catch()  
        .catch((e) => {
          console.log(e);
        });
    },
    //     
    updateDiary() {
      // axios  
      DiaryDataService.update(this.currentDiary.dno, this.currentDiary)
        //  then()  
        .then((response) => {
          console.log(response.data);
          // (__)   : /diary
          // this.$router.push("/myPage");
          // location.reload();
          this.retrieveDiary(); //     
        })
        //  .catch()  
        .catch((e) => {
          console.log(e);
        });
    },
    //     
    deleteDiary() {
      // axios  
      DiaryDataService.delete(this.currentDiary.dno, this.currentDiary)
        //  then()  
        .then((response) => {
          console.log(response.data);
          // (__)   : /diary
          // this.$router.push("/myPage");
          // location.reload();
          this.retrieveDiary(); //     
        })
        //  .catch()  
        .catch((e) => {
          console.log(e);
        });
    },
  },

  mounted() {
    this.retrieveDiary(); //     

    this.message = "";
    //  this.$route.params.no :    $route.params  
    // $route  :  url   
    // router/index.js  url  : :no
    this.getDiary(this.$route.params.dno);
  },
};
</script>

<style>
@import url(@/assets/css/style.css);
.diaryList {
  width: 800px;
  margin: auto;
}
.editDiary {
  width: 900px;
  margin: auto;
}
</style>
