// DiaryDataService.js
//  : Diary  axios  CRUD     
// import http from "../services/DiaryDataService";
import http from "../http-common";

class DiaryDataService {
    //     
    getAll(id) {
        // get    -> @GetMapping("/api/diary")
        return http.get(`/diary/all/${id}`); 
    }

    //    
    // get    -> @GetMapping("/api/diary/{no}"), @PathVariable
    get(dno) {
        return http.get(`/diary/${dno}`)
    }

    //  (insert)  
    // post    -> @PostMapping("/api/diary"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/diary", data);
    }

    //  (update)  
    // put    -> @PutMapping("/api/diary/{no}"), @RequestBody
    update(dno, data) {
        alert(" .");
        return http.put(`/diary/${dno}`, data);
    }

    //  (delete)  
    // delete    -> @DeleteMapping("/api/diary/deletion/{no}")
    //                        , @PathVariable  
    delete(dno) {
        alert(" .");
        return http.delete(`/diary/deletion/${dno}`);
    }

    //     
    // delete    -> @DeleteMapping("/api/diary/all")
    deleteAll() {
        return http.delete("/diary/all")
    }


}

export default new DiaryDataService();