// TodoDataService.js
//  : Todo  axios  CRUD     
// import http from "../services/TodoDataService";
import http from "../http-common";

class TodoDataService {
    //    
    getAll(id) {
        // get    -> @GetMapping("/api/todo")
        return http.get(`/todo/all/${id}`); 
    }

    //    
    // get    -> @GetMapping("/api/todo/{no}"), @PathVariable
    get(tno) {
        return http.get(`/todo/${tno}`)
    }

    //  (insert)  
    // post    -> @PostMapping("/api/todo"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/todo", data);
    }

    //  (update)  
    // put    -> @PutMapping("/api/todo/{no}"), @RequestBody
    update(tno, data) {
        alert(" .");
        return http.put(`/todo/${tno}`, data);
    }

    //  (delete)  
    // delete    -> @DeleteMapping("/api/todo/deletion/{no}")
    //                        , @PathVariable  
    delete(tno) {
        return http.delete(`/todo/deletion/${tno}`);
    }

    //     
    // delete    -> @DeleteMapping("/api/todo/all")
    deleteAll() {
        return http.delete("/todo/all")
    }


}

export default new TodoDataService();