import http from "../http-common";

class FbLikeService {
 
  //  
  getLike(fno, userId) {
    return http.get(`/fblike/${fno}/${userId}`);
  }
  // TODO : count like
  countLike(fno) {
    return http.get(`/fblike/${fno}`);
  }


  //  
  create(data) {
    console.log(data);
    return http.post('/fblike', data);
  }

  delete(fbLikeId) {
    return http.delete(`/fblike/deletion/${fbLikeId}`);
}

}

export default new FbLikeService();
